@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
* {
  box-sizing: border-box; }

div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video, button, select {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.42857143;
  color: #333;
  background-color: #fff;
  margin: 0; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

strong {
  font-weight: bold; }

img {
  display: block;
  max-width: 100%;
  height: auto;
  border: 0; }

html, body {
  height: 100%;
  min-height: 100%; }

#app {
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  padding-bottom: 55px; }
  #app .app-header {
    text-align: center; }
    #app .app-header h1 {
      max-width: 300px;
      margin: 0 auto; }
      #app .app-header h1 img {
        width: 100%;
        transform-style: preserve-3d;
        transition: all 0.2s linear; }
      #app .app-header h1 .image-animated {
        -webkit-animation: dance 1 1s linear;
        -o-animation: dance 1 1s linear;
        -moz-animation: dance 1 1s linear;
        -ms-animation: dance 1 1s linear;
        animation: dance 1 1s linear; }
      @media screen and (max-width: 640px) {
        #app .app-header h1 {
          max-width: 200px; } }
  #app .settings {
    background: #3c0058;
    margin: 20px 0;
    padding: 40px 0; }
    #app .settings .url-master {
      margin-bottom: 20px; }
      #app .settings .url-master button {
        background: transparent;
        color: #eee;
        border-color: #eee; }
      #app .settings .url-master .text-btn {
        font-size: 14px; }
    #app .settings .checkbox-inline {
      margin-left: 0;
      margin-right: 10px;
      color: white; }
  #app .introduction-personalize {
    color: white;
    margin: 10px 0; }
    #app .introduction-personalize .linha {
      margin-top: 5px; }
    #app .introduction-personalize span {
      width: 100%;
      height: 1px;
      display: inline-block;
      background: white;
      vertical-align: middle;
      float: none; }
    #app .introduction-personalize p {
      margin: 5px;
      font-style: italic; }
  #app .personalize .form-control {
    display: inline-block;
    width: auto;
    width: 70px; }
  #app .personalize p {
    display: inline-block;
    margin: 0 5px;
    color: white; }
  #app .personalize button {
    margin-left: 10px;
    background: transparent;
    color: #eee;
    border: 1px solid #eee; }
  #app .iframes {
    padding-top: 15px; }
    #app .iframes .case-resolutions .resolution {
      margin-right: auto;
      margin-left: auto;
      line-height: 0; }
      #app .iframes .case-resolutions .resolution .panel-heading, #app .iframes .case-resolutions .resolution .panel-footer {
        background: #3c0058;
        color: white; }
      #app .iframes .case-resolutions .resolution .panel-body {
        padding: 5px 0;
        overflow-x: auto;
        width: 100%; }
        #app .iframes .case-resolutions .resolution .panel-body iframe {
          border: 0;
          padding: 0;
          margin: 0; }
  #app .footer {
    background: #700e9e;
    margin-top: 20px;
    position: absolute;
    width: 100%;
    box-sizing: border-box;
    padding: 0 15px;
    bottom: 0; }
    #app .footer p {
      color: white;
      margin: 10px 0; }
      #app .footer p a {
        color: #ff0496; }
  #app .back-to-top {
    cursor: pointer;
    position: fixed;
    bottom: 42px;
    right: 20px;
    display: none;
    background: #3c0058; }

@-webkit-keyframes dance {
  0%, 10%, 20% {
    -webkit-transform: rotateZ(10deg); }
  5%, 15%, 25% {
    -webkit-transform: rotateZ(-10deg); }
  35%, 60%, 100% {
    -webkit-transform: scale(1) rotateZ(0deg);
    box-shadow: inset 0 0 0 0 transparent; }
  40% {
    -webkit-transform: scale(1.3);
    box-shadow: inset 0 0 0 0; } }

@-moz-keyframes dance {
  0%, 10%, 20% {
    -moz-transform: rotateZ(10deg); }
  5%, 15%, 25% {
    -moz-transform: rotateZ(-10deg); }
  35%, 60%, 100% {
    -moz-transform: scale(1) rotateZ(0deg);
    box-shadow: inset 0 0 0 0 transparent; }
  40% {
    -moz-transform: scale(1.3);
    box-shadow: inset 0 0 0 0; } }

@-ms-keyframes dance {
  0%, 10%, 20% {
    -ms-transform: rotateZ(10deg); }
  5%, 15%, 25% {
    -ms-transform: rotateZ(-10deg); }
  35%, 60%, 100% {
    -ms-transform: scale(1) rotateZ(0deg);
    box-shadow: inset 0 0 0 0 transparent; }
  40% {
    -ms-transform: scale(1.3);
    box-shadow: inset 0 0 0 0; } }

@-o-keyframes dance {
  0%, 10%, 20% {
    -o-transform: rotateZ(10deg); }
  5%, 15%, 25% {
    -o-transform: rotateZ(-10deg); }
  35%, 60%, 100% {
    -o-transform: scale(1) rotateZ(0deg);
    box-shadow: inset 0 0 0 0 transparent; }
  40% {
    -o-transform: scale(1.3);
    box-shadow: inset 0 0 0 0; } }

@keyframes dance {
  0%, 10%, 20% {
    transform: rotateZ(10deg); }
  5%, 15%, 25% {
    transform: rotateZ(-10deg); }
  35%, 60%, 100% {
    transform: scale(1) rotateZ(0deg);
    box-shadow: inset 0 0 0 0 transparent; }
  40% {
    transform: scale(1.3);
    box-shadow: inset 0 0 0 0; } }
